import { useState, ChangeEvent, FormEvent } from "react";
import { Form, Input, Button, Spacer } from "@nextui-org/react";

interface FormValues {
  name: string;
  birthdate: string;
  email: string;
  whatsapp: string;
  instagram: string;
  telegram: string;
  country: string;
  state: string;
  city: string;
  street: string;
  num_ext: string;
  num_int: string;
  cp: string;
  reference: string;
}

interface FormErrors {
  [key: string]: string;
}

const Profile = () => {
  const [values, setValues] = useState<FormValues>({
    name: "",
    birthdate: "",
    email: "",
    whatsapp: "",
    instagram: "",
    telegram: "",
    country: "",
    state: "",
    city: "",
    street: "",
    num_ext: "",
    num_int: "",
    cp: "",
    reference: "",
  });

  const [errors, setErrors] = useState<FormErrors>({});

  const validate = (): boolean => {
    const newErrors: FormErrors = {};
    if (!values.name) newErrors.name = "El nombre es obligatorio";
    if (!values.birthdate)
      newErrors.birthdate = "La fecha de nacimiento es obligatoria";
    if (!values.email) {
      newErrors.email = "El correo es obligatorio";
    } else if (!/^[\w-.]+@[\w-]+\.[a-z]{2,}$/i.test(values.email)) {
      newErrors.email = "El correo no es válido";
    }
    if (!values.whatsapp)
      newErrors.whatsapp = "El número de WhatsApp es obligatorio";
    if (!values.country) newErrors.country = "El país es obligatorio";
    if (!values.state) newErrors.state = "El estado es obligatorio";
    if (!values.city) newErrors.city = "La ciudad es obligatoria";
    if (!values.street) newErrors.street = "La calle es obligatoria";
    if (!values.cp) newErrors.cp = "El código postal es obligatorio";
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
    setErrors((prev) => ({ ...prev, [name]: "" })); // Limpiar error al escribir
  };

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (validate()) {
      console.log("Datos enviados:", values);
      // Aquí puedes enviar los datos a tu backend o procesarlos según sea necesario
    }
  };

  return (
    <div className="border border-1 p-4 relative rounded-lg border-[#3f3f3f]">
      <Form onSubmit={handleSubmit}>
        <span>Datos personales</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            label="Nombre"
            name="name"
            value={values.name}
            onChange={handleChange}
            errorMessage={errors.name}
            isInvalid={!!errors.name}
            variant="bordered"
          />

          <Input
            className=""
            type="date"
            label="Fecha de nacimiento"
            name="birthdate"
            value={values.birthdate}
            onChange={handleChange}
            errorMessage={errors.birthdate}
            isInvalid={!!errors.birthdate}
            variant="bordered"
          />

          <Input
            className=""
            type="email"
            label="Correo electrónico"
            name="email"
            value={values.email}
            onChange={handleChange}
            errorMessage={errors.email}
            isInvalid={!!errors.email}
            variant="bordered"
          />
        </div>
        <Spacer y={1} />
        <span>Redes Sociales</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            type="number"
            label="WhatsApp"
            name="whatsapp"
            value={values.whatsapp}
            onChange={handleChange}
            errorMessage={errors.whatsapp}
            isInvalid={!!errors.whatsapp}
            variant="bordered"
          />

          <Input
            className=""
            label="Instagram"
            name="instagram"
            value={values.instagram}
            onChange={handleChange}
            variant="bordered"
          />

          <Input
            className=""
            label="Telegram"
            name="telegram"
            value={values.telegram}
            onChange={handleChange}
            variant="bordered"
          />
        </div>
        <Spacer y={1} />
        <span>Direccion</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            label="País"
            name="country"
            value={values.country}
            onChange={handleChange}
            errorMessage={errors.country}
            isInvalid={!!errors.country}
            variant="bordered"
          />

          <Input
            className=""
            label="Estado"
            name="state"
            value={values.state}
            onChange={handleChange}
            errorMessage={errors.state}
            isInvalid={!!errors.state}
            variant="bordered"
          />

          <Input
            className=""
            label="Ciudad"
            name="city"
            value={values.city}
            onChange={handleChange}
            errorMessage={errors.city}
            isInvalid={!!errors.city}
            variant="bordered"
          />

          <Input
            className=""
            label="Calle"
            name="street"
            value={values.street}
            onChange={handleChange}
            errorMessage={errors.street}
            isInvalid={!!errors.street}
            variant="bordered"
          />

          <Input
            className=""
            label="Número exterior"
            name="num_ext"
            value={values.num_ext}
            onChange={handleChange}
            variant="bordered"
          />

          <Input
            className=""
            label="Número interior"
            name="num_int"
            value={values.num_int}
            onChange={handleChange}
            variant="bordered"
          />

          <Input
            className=""
            label="Código postal"
            name="cp"
            value={values.cp}
            onChange={handleChange}
            errorMessage={errors.cp}
            isInvalid={!!errors.cp}
            variant="bordered"
          />

          <Input
            className=""
            label="Referencia"
            name="reference"
            value={values.reference}
            onChange={handleChange}
            variant="bordered"
          />
        </div>
        <Spacer y={1} />
        <Button type="submit" color="primary" size="lg">
          Guardar
        </Button>
      </Form>
    </div>
  );
};

export default Profile;
